'use client';

import { useState, useEffect } from 'react';
import styles from './PageStyle.module.css';

interface Train {
  id: number;
  trainName: string;
  trainNumber: string;
  departureCity: string;
  arrivalCity: string;
  departureTime: string;
  arrivalTime: string;
  duration: string;
}

export default function UserPage() {
  const [trains, setTrains] = useState<Train[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortColumn, setSortColumn] = useState<keyof Train | null>(null);
  const [sortDirection, setSortDirection] = useState<'asc' | 'desc'>('asc');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // load data from APi
  useEffect(() => {
    const fetchTrains = async () => {
      try {
        const response = await fetch('http://localhost:3009/trains');
        if (!response.ok) {
          throw new Error('Unable to upload data');
        }
        const data: Train[] = await response.json();
        setTrains(data);
        setLoading(false);
      } catch (err) {
        setError('ERROR to upload data');
        setLoading(false);
      }
    };

    fetchTrains();
  }, []);

  // sort
  const handleSort = (column: keyof Train) => {
    const newDirection = sortColumn === column && sortDirection === 'asc' ? 'desc' : 'asc';
    setSortColumn(column);
    setSortDirection(newDirection);

    const sortedTrains = [...trains].sort((a, b) => {
      const valueA = a[column];
      const valueB = b[column];
      if (newDirection === 'asc') {
        return valueA > valueB ? 1 : -1;
      } else {
        return valueA < valueB ? 1 : -1;
      }
    });
    setTrains(sortedTrains);
  };

  // find
  const filteredTrains = trains.filter((train) =>
    train.trainName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return <div className={styles.container}>Завантаження...</div>;
  }

  if (error) {
    return <div className={styles.container}>{error}</div>;
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Train schedule</h1>

      <div className={styles.searchWrapper}>
        <input
          type="text"
          placeholder="Enter train name..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className={styles.searchInput}
        />
      </div>

      <table className={styles.trainTable}>
      <thead>
          <tr className={styles.tableHeader}>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('id')}>
              ID {sortColumn === 'id' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('trainName')}>
              Train name {sortColumn === 'trainName' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('trainNumber')}>
              Number {sortColumn === 'trainNumber' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('departureCity')}>
              City departure {sortColumn === 'departureCity' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('arrivalCity')}>
              City arrivalCity {sortColumn === 'arrivalCity' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('departureTime')}>
              Time departure {sortColumn === 'departureTime' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('arrivalTime')}>
              Time arriaval {sortColumn === 'arrivalTime' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
            <th className={styles.tableHeaderCell} onClick={() => handleSort('duration')}>
              Duration {sortColumn === 'duration' && (sortDirection === 'asc' ? '↑' : '↓')}
            </th>
          </tr>
        </thead>

        <tbody>
          {filteredTrains.map((train) => (
            <tr key={train.id} className={styles.tableRow}>
              <td className={styles.tableCell}>{train.id}</td>
              <td className={styles.tableCell}>{train.trainName}</td>
              <td className={styles.tableCell}>{train.trainNumber}</td>
              <td className={styles.tableCell}>{train.departureCity}</td>
              <td className={styles.tableCell}>{train.arrivalCity}</td>
              <td className={styles.tableCell}>{train.departureTime}</td>
              <td className={styles.tableCell}>{train.arrivalTime}</td>
              <td className={styles.tableCell}>{train.duration}</td>
            </tr>
          ))}
        </tbody>

      </table>
    </div>
  );
}